import { FC } from "react";
import { Card } from "@doar/components";
import { DefaultPalette } from "@fluentui/react/lib/Styling";
import { GroupedVerticalBarChart } from "@fluentui/react-charting";

const AgeGenderChart: FC = () => {
    const chartData = [
        { name: "1-10 years", male: 2500, female: 1200 },
        { name: "11-18 years", male: 5000, female: 5500 },
        { name: "19-30 years", male: 15500, female: 13475 },
        { name: "31-40 years", male: 19250, female: 19865 },
        { name: "41-50 years", male: 10000, female: 8900 },
        { name: "51-60 years", male: 5800, female: 5000 },
        { name: "61-70 years", male: 3700, female: 2100 },
        { name: "Above 70 years", male: 2300, female: 1800 },
    ];

    const chartDataForChart: any = chartData.map((item) => {
        return {
            name: item.name,
            series: [
                {
                    key: "series1",
                    data: item.male,
                    color: DefaultPalette.purpleLight,
                    legend: "Male",
                },
                {
                    key: "series2",
                    data: item.female,
                    color: DefaultPalette.purple,
                    legend: "Female",
                },
            ],
        };
    });

    let rootElem: HTMLElement | null | undefined;

    return (
        <Card height="100%">
            <div
                ref={(e: HTMLElement | null) => {
                    rootElem = e;
                }}
                style={{
                    height: "100%",
                    width: "100%",
                    position: "absolute",
                    right: 0,
                    top: 0,
                }}
            >
                <GroupedVerticalBarChart
                    parentRef={rootElem}
                    culture={window.navigator.language}
                    chartTitle="Grouped Vertical Bar chart basic example"
                    data={chartDataForChart}
                    barwidth={10}
                    showYAxisGridLines
                    wrapXAxisLables
                />
            </div>
        </Card>
    );
};

export default AgeGenderChart;
