import {
    Row,
    Col,
    TabWrap,
    TabList,
    Tab,
    TabContent,
    TabPanel,
    Card,
} from "@doar/components";
import Scrollbar from "src/components/scrollbar";
import { FC } from "react";
import WelcomeArea from "src/layouts/welcome-area";
import NCQADropdowns from "./ncqa.dropdowns";
import { StyledBody } from "./style";
import DashboardTab from "./dashboardTab";
import StandardsComplianceTab from "./standardsComplianceTab";
import ReportTab from "./reportTab";

const AccreditationMain: FC = () => {
    return (
        <>
            <WelcomeArea
                welcomeText="NCQA Accreditation"
                title="NCQA Accreditation"
                prevTitle="HEQ 360"
            />
            <Row gutters={10}>
                <NCQADropdowns />
            </Row>
            <Row gutters={10}>
                <Col lg={12} xl={12} mt="5px">
                    <div style={{ height: 700, minHeight: "500" }}>
                        <StyledBody $layout={1}>
                            <TabWrap variation="line">
                                <TabList>
                                    <Tab>Dashboard</Tab>
                                    <Tab>Standards Compliance</Tab>
                                    <Tab>Reports</Tab>
                                </TabList>
                                <TabContent>
                                    <Scrollbar top="0">
                                        <TabPanel>
                                            <DashboardTab />
                                        </TabPanel>
                                        <TabPanel>
                                            <StandardsComplianceTab />
                                        </TabPanel>
                                        <TabPanel>
                                            <ReportTab />
                                        </TabPanel>
                                    </Scrollbar>
                                </TabContent>
                            </TabWrap>
                        </StyledBody>
                    </div>
                </Col>
            </Row>
        </>
    );
};

export default AccreditationMain;
