import { FC } from "react";
import { Card } from "@doar/components";
import { DefaultPalette } from "@fluentui/react/lib/Styling";
import { GroupedVerticalBarChart } from "@fluentui/react-charting";

const DEITrainingChart: FC = () => {
    const chartData = [
        {
            name: "Quality Management",
            complete: 12,
            pending: 4,
            pastDue: 2,
        },
        { name: "Marketing", complete: 46, pending: 20, pastDue: 8 },
        {
            name: "Care Management",
            complete: 70,
            pending: 18,
            pastDue: 4,
        },
        {
            name: "Network Management",
            complete: 9,
            pending: 4,
            pastDue: 1,
        },
        {
            name: "Customer Service",
            complete: 104,
            pending: 42,
            pastDue: 15,
        },
        { name: "Sales", complete: 32, pending: 12, pastDue: 5 },
        { name: "Human Resources", complete: 8, pending: 2, pastDue: 0 },
        {
            name: "Information Technology",
            complete: 34,
            pending: 13,
            pastDue: 9,
        },
    ];

    const chartDataForChart: any = chartData.map((item) => {
        return {
            name: item.name,
            series: [
                {
                    key: "series1",
                    data: item.complete,
                    color: DefaultPalette.tealDark,
                    legend: "Complete",
                },
                {
                    key: "series2",
                    data: item.pending,
                    color: DefaultPalette.tealLight,
                    legend: "Pending",
                },
                {
                    key: "series3",
                    data: item.pastDue,
                    color: DefaultPalette.teal,
                    legend: "Past Due",
                },
            ],
        };
    });

    console.log(chartDataForChart);

    let rootElem: HTMLElement | null | undefined;

    return (
        <Card height="100%">
            <div
                ref={(e: HTMLElement | null) => {
                    rootElem = e;
                }}
                style={{
                    height: "100%",
                    width: "100%",
                    position: "absolute",
                    right: 0,
                    top: 0,
                }}
            >
                <GroupedVerticalBarChart
                    parentRef={rootElem}
                    culture={window.navigator.language}
                    chartTitle="Grouped Vertical Bar chart basic example"
                    data={chartDataForChart}
                    barwidth={10}
                    showYAxisGridLines
                    wrapXAxisLables
                />
            </div>
        </Card>
    );
};

export default DEITrainingChart;
