import { FC } from "react";
import { Card, SectionTitle } from "@doar/components";
import {
    StyledHeader,
    StyledHeaderRight,
    StyledIcon,
    StyledFooter,
    StyledFooterLink,
} from "./style";

interface ITransaction {
    cardtitle: string;
    component: any;
}
const Transaction: FC<ITransaction> = (props: ITransaction) => {
    const { cardtitle, component: Component } = props;
    return (
        <Card height="100%">
            <StyledHeader>
                <SectionTitle title={cardtitle} />
                <StyledHeaderRight>
                    <StyledIcon href="#" aria-label="refesh">
                        <i className="fa fa-redo" />
                    </StyledIcon>
                    <StyledIcon href="#" ml="10px" aria-label="more options">
                        <i className="fa fa-ellipsis-v" />
                    </StyledIcon>
                </StyledHeaderRight>
            </StyledHeader>
            <div style={{ height: 300 }}>
                <Component />
            </div>
            <StyledFooter>
                <StyledFooterLink href="/profile-view">
                    View More Details
                    <i className="icon ion-md-arrow-down mg-l-5" />
                </StyledFooterLink>
            </StyledFooter>
        </Card>
    );
};

export default Transaction;
