const toLocalDate = (params: any) => {
    const { value } = params;
    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    return value && value.toLocaleDateString();
};

export const getColumnFields = () => {
    const columnFields: (ColDef | ColGroupDef)[] = [
        {
            field: "standard",
            headerName: "Standard",
        },
        {
            headerName: "Element",
            field: "element",
        },
        {
            headerName: "File Name",
            field: "fileName",
        },
        {
            headerName: "Effective Date",
            field: "effectiveDate",
            cellRenderer: toLocalDate,
        },
        {
            headerName: "Renewal Date",
            field: "renewalDate",
            cellRenderer: toLocalDate,
        },
        {
            headerName: "Status",
            field: "status",
        },
        {
            headerName: "Products Assigned",
            field: "productsAssigned",
        },
        {
            headerName: "Factors Assigned",
            field: "factorsAssigned",
        },
    ];
    return columnFields;
};
