import { AgGridReact } from "ag-grid-react";
import { ColDef, ColGroupDef } from "ag-grid-community";
import { useCallback, useState } from "react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-alpine.css";

const NotForProfitInitiatives = () => {
    const getLink = (item: any) => {
        return (
            // eslint-disable-next-line react/jsx-no-target-blank
            <a href={item.value} target="_blank">
                {item.value}
            </a>
        );
    };
    const LinkRender = (item: any) => {
        return (
            <>
                {getLink(item)} <br />
            </>
        );
    };
    const columnFields: (ColDef | ColGroupDef)[] = [
        {
            field: "agency",
            headerName: "Organization",
            width: 300,
        },
        {
            field: "resource",
            headerName: "Resource URL",
            filter: false,
            width: 800,
            cellRenderer: LinkRender,
        },
    ];

    const tableRowData = [
        {
            agency:
                "University of Wisconsin-Madison Population Health Institute",
            resource:
                "https://uwphi.pophealth.wisc.edu/match/health-equity-training-modules/",
        },
        {
            agency: "CDC Foundation",
            resource:
                "https://www.cdcfoundation.org/blog/building-culture-health-equity-new-initiative-launches-10-state-health-agencies",
        },
        {
            agency: "National Academy for State Health Policy",
            resource:
                "https://www.nashp.org/resources-for-states-to-address-health-equity-and-disparities/",
        },
        {
            agency: "Health Equity Initiative",
            resource: "https://www.healthequityinitiative.org/",
        },
        {
            agency: "Kentuckiana Health Collaborative",
            resource: "https://khcollaborative.org/programs/health-equity/",
        },
    ];
    const [columnDefs, setColumnDefs] = useState(columnFields);

    const [rowData, setRowData] = useState(tableRowData);

    const gridRef: any = useState();

    const onGridReady = useCallback((params) => {
        gridRef.current.api.sizeColumnsToFit();
    }, []);

    return (
        <>
            <AgGridReact
                pagination
                rowData={rowData}
                columnDefs={columnDefs}
                domLayout="autoHeight"
                className="ag-theme-alpine"
                ref={gridRef}
                onGridReady={onGridReady}
            />
        </>
    );
};

export default NotForProfitInitiatives;
