import { FC } from "react";
import { Row, Col } from "@doar/components";
import WelcomeArea from "src/layouts/welcome-area";
import Transaction from "./transaction";
import WorkspaceChart from "../metrics/workspace.chart";
import AgeGenderChart from "./ageGenderChart";
import DEITrainingChart from "./deiTrainingChart";
import SDOHFactorChart from "./sdohFactorChart";
import Tasks from "./tasks";
import EventsCard from "./events";

const RowOne = () => {
    return (
        <>
            <Col md={6} xl={4} mt="10px">
                <Transaction
                    cardtitle="Race-Ethnicity"
                    component={WorkspaceChart}
                />
            </Col>
            <Col md={6} xl={4} mt="10px">
                <Transaction
                    cardtitle="Member Age-Gender"
                    component={AgeGenderChart}
                />
            </Col>
            <Col md={6} xl={4} mt="10px">
                <Transaction
                    cardtitle="DEI Training"
                    component={DEITrainingChart}
                />
            </Col>
        </>
    );
};

const RowTwo = () => {
    return (
        <>
            <Col md={6} xl={4} mt="10px">
                <Transaction
                    cardtitle="SDOH Factors"
                    component={SDOHFactorChart}
                />
            </Col>
            <Col md={6} xl={4} mt="10px">
                <Tasks />
            </Col>
            <Col md={6} xl={4} mt="10px">
                <EventsCard />
            </Col>
        </>
    );
};
const DashboardMain: FC = () => {
    return (
        <>
            <WelcomeArea
                welcomeText="Dashboard"
                title="Dashboard"
                prevTitle="HEQ 360"
            />
            <Row gutters={10}>
                <RowOne />
                <RowTwo />
            </Row>
        </>
    );
};

export default DashboardMain;
