import { ColDef, ColGroupDef } from "ag-grid-community";
import { Col, Row } from "@doar/components";
import { Download, Edit, Eye, Star } from "react-feather";

export interface IStandardComplianceSummaryTable {
    standard: string;
    element: string;
    status: "Incomplete";
    approval: string;
    action: string;
}

export const metricsData: IStandardComplianceSummaryTable[] = [
    {
        standard: "Organizational Readiness",
        element: "A. Build a Diverse Staff",
        status: "Incomplete",
        approval: "NA",
        action: "",
    },
    {
        standard: "Organizational Readiness",
        element: "B. Promoting Diversity, Equity and Inclusion Among Staff",
        status: "Incomplete",
        approval: "NA",
        action: "",
    },
];

const toLocalDate = (params: any) => {
    const { value } = params;
    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    return value && value.toLocaleDateString();
};

const renderActions = () => {
    return (
        <Row>
            <Col lg={3} xl={3} mt="10px">
                <Edit color="blue" strokeWidth={1} size={22} />
            </Col>
        </Row>
    );
};

const renderStar = (item: any) => {
    if (item && item.value) {
        return <Star strokeWidth={1} fill="black" />;
    }
    return <Star strokeWidth={1} />;
};

export const getColumnFields = () => {
    const columnFields: (ColDef | ColGroupDef)[] = [
        {
            field: "standard",
            headerName: "Standard",
        },
        {
            headerName: "Element",
            field: "element",
        },
        {
            headerName: "Status",
            field: "status",
        },
        {
            headerName: "Approval",
            field: "approval",
        },
        {
            headerName: "Action",
            field: "action",
            cellRenderer: renderActions,
        },
    ];
    return columnFields;
};
