import { ColDef, ColGroupDef } from "ag-grid-community";
import { Col, Row } from "@doar/components";
import { Download, Eye, Star } from "react-feather";

export interface IMetricsSummaryTable {
    isFavorite: boolean;
    report: string;
    startDate: Date;
    endDate: Date;
    assignee: string;
    publishedOn: Date;
    status: "Active" | "Out of Date" | "Archived";
    action: string;
    type: "Restricted" | "Normal";
}

export const metricsData: IMetricsSummaryTable[] = [
    {
        isFavorite: false,
        report: "Staff Demographics - Race & Ethnicity",
        startDate: new Date(),
        endDate: new Date(),
        assignee: "Mary Jones",
        publishedOn: new Date(),
        status: "Active",
        action: "",
        type: "Restricted",
    },
    {
        isFavorite: true,
        report: "Language Capability",
        startDate: new Date(),
        endDate: new Date(),
        assignee: "Beth Page",
        publishedOn: new Date(),
        status: "Active",
        action: "",
        type: "Normal",
    },
];

const toLocalDate = (params: any) => {
    const { value } = params;
    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    return value && value.toLocaleDateString();
};

const renderActions = () => {
    return (
        <Row>
            <Col lg={3} xl={3} mt="10px">
                <Eye color="blue" strokeWidth={1} size={22} />
            </Col>
            <Col lg={3} xl={3} mt="10px">
                <Download color="red" strokeWidth={1} size={22} />
            </Col>
        </Row>
    );
};

const renderStar = (item: any) => {
    if (item && item.value) {
        return <Star strokeWidth={1} fill="black" />;
    }
    return <Star strokeWidth={1} />;
};

export const getColumnFields = () => {
    const columnFields: (ColDef | ColGroupDef)[] = [
        {
            field: "isFavorite",
            cellRenderer: renderStar,
            headerComponent: renderStar,
            filter: false,
            width: 70,
        },
        {
            headerName: "Report",
            field: "report",
        },
        {
            headerName: "Start Date",
            field: "startDate",
            width: 100,
            cellRenderer: toLocalDate,
        },
        {
            headerName: "End Date",
            field: "endDate",
            width: 100,
            cellRenderer: toLocalDate,
        },
        {
            headerName: "Assignee",
            field: "assignee",
            width: 120,
        },
        {
            headerName: "Published On",
            field: "publishedOn",
            cellRenderer: toLocalDate,
            width: 100,
        },
        {
            headerName: "Status",
            field: "status",
            width: 80,
        },
        {
            headerName: "Action",
            field: "action",
            cellRenderer: renderActions,
            width: 100,
        },
        {
            headerName: "Type",
            field: "type",
            width: 100,
        },
    ];
    return columnFields;
};
