import {
    Row,
    Col,
    TabWrap,
    TabList,
    Tab,
    TabContent,
    TabPanel,
} from "@doar/components";
import { FC } from "react";
import WelcomeArea from "src/layouts/welcome-area";
import Scrollbar from "src/components/scrollbar";
import { StyledMain, StyledBody } from "./style";
import WorkForceMain from "./workforce.main";

const MetricsMain: FC = () => {
    return (
        <>
            <WelcomeArea
                welcomeText="Metrics"
                title="Metrics"
                prevTitle="HEQ 360"
            />
            <Row gutters={10}>
                <Col lg={12} xl={12} mt="5px">
                    <div style={{ height: 900 }}>
                        <StyledBody $layout={1}>
                            <TabWrap variation="line">
                                <TabList>
                                    <Tab>Workforce</Tab>
                                    <Tab>Access</Tab>
                                    <Tab>Utilization</Tab>
                                    <Tab>Quality</Tab>
                                    <Tab>SDOH</Tab>
                                    <Tab>PARTNERS</Tab>
                                </TabList>
                                <TabContent>
                                    <Scrollbar top="0">
                                        <TabPanel>
                                            <WorkForceMain />
                                        </TabPanel>
                                        <TabPanel>
                                            <div>Access Tab</div>
                                        </TabPanel>
                                        <TabPanel>
                                            <div>Utilization Tab</div>
                                        </TabPanel>
                                        <TabPanel>
                                            <div>SDOH Tab</div>
                                        </TabPanel>
                                        <TabPanel>
                                            <div>SDOH Tab</div>
                                        </TabPanel>
                                        <TabPanel>
                                            <div>PARTNERS Tab</div>
                                        </TabPanel>
                                    </Scrollbar>
                                </TabContent>
                            </TabWrap>
                        </StyledBody>
                    </div>
                </Col>
            </Row>
            <Row />
        </>
    );
};

export default MetricsMain;
