import { FC } from "react";
import { Card } from "@doar/components";
import { DefaultPalette } from "@fluentui/react/lib/Styling";
import { GroupedVerticalBarChart } from "@fluentui/react-charting";

const SDOHFactorChart: FC = () => {
    const chartData = [
        {
            name: "Physical Environment",
            medicare: 10.0,
            medicaid: 18.0,
            exchange: 8.0,
        },
        {
            name: "Socio-Economic Factors",
            medicare: 40.0,
            medicaid: 46.0,
            exchange: 35.0,
        },
        {
            name: "Health Behaviors",
            medicare: 30.0,
            medicaid: 22.0,
            exchange: 25.0,
        },
        {
            name: "Clinical Care",
            medicare: 20.0,
            medicaid: 14.0,
            exchange: 32.0,
        },
    ];

    const chartDataForChart: any = chartData.map((item) => {
        return {
            name: item.name,
            series: [
                {
                    key: "series1",
                    data: item.medicare,
                    color: DefaultPalette.blueLight,
                    legend: "Medicare",
                },
                {
                    key: "series2",
                    data: item.medicaid,
                    color: DefaultPalette.blueDark,
                    legend: "Medicaid",
                },
                {
                    key: "series3",
                    data: item.exchange,
                    color: DefaultPalette.blueMid,
                    legend: "Exchange",
                },
            ],
        };
    });

    let rootElem: HTMLElement | null | undefined;

    return (
        <Card height="100%">
            <div
                ref={(e: HTMLElement | null) => {
                    rootElem = e;
                }}
                style={{
                    height: "100%",
                    width: "100%",
                    position: "absolute",
                    right: 0,
                    top: 0,
                }}
            >
                <GroupedVerticalBarChart
                    parentRef={rootElem}
                    culture={window.navigator.language}
                    chartTitle="Grouped Vertical Bar chart basic example"
                    data={chartDataForChart}
                    barwidth={10}
                    showYAxisGridLines
                    wrapXAxisLables
                />
            </div>
        </Card>
    );
};

export default SDOHFactorChart;
